<template lang='pug'>
  button.button(
    :class='[\
      dark ? "button_dark" : "button_light",\
      { disabled: disabled },\
      small ? "button_small" : "",\
      textSmall ? "button_text_small" : "",\
    ]'
    :disabled='disabled'
  )
    img.button__icon(:src='src' v-if='src')
    | {{ text }}
</template>

<style lang="scss" scoped>
  $button-width:        250px;
  $button-height:       70px;

  $button-small-width:  128px;
  $button-small-height: 42px;

  $button-media-width:  190px;
  $button-media-height: 50px;

  .button {
    box-sizing: border-box;

    width: $button-width;
    height: $button-height;

    padding: 0;
    border-width: $border-thin;
    border-radius: $tiny-border-rd;

    font-size: $big-context;
    font-family: $default-font;
    text-align: center;

    outline: 0;

    transition: $fast-tn-delay $normal-time-func;

    &_light {
      border: $border-thin solid $border-second-color;

      color: $dark-second-color;

      background-color: $light-color;

      &:not([disabled]):not(.disabled):hover {
        background-color: $light-second-color;
      }

      &:not([disabled]):not(.disabled):active {
        color: $dark-color;

        background-color: $light-dark-color;
      }
    }

    &_dark {
      border: 0;

      color: $light-color;

      background-color: $info-color;

      &:hover {
        background-color: $info-second-color;
      }

      &:active {
        background-color: $info-dark-color;
      }
    }

    &_text_small {
      font-size: $medium-context;
    }

    &_small {
      width: $button-small-width;
      height: $button-small-height;

      font-size: $small-context;
    }

    &:not([disabled]):not(.disabled) {
      cursor: pointer;
    }

    &:disabled,
    &.disabled {
      color: $disable-text-second-color;

      background-color: $disable-bg-second-color;

      cursor: not-allowed;
    }
  }

  @media (max-width: 996px) {

    .button {
      width: $button-media-width;
      height: $button-media-height;

      font-size: $big-context;
    }
  }
</style>

<script>
export default {
  props: {
    dark: {
      type: Boolean,
      required: false,
    },
    disabled: {
      type: Boolean,
      required: false,
    },
    text: {
      type: String,
      required: false,
      default: 'Button',
    },
    small: {
      type: Boolean,
    },
    textSmall: {
      type: Boolean,
    },
    src: {
      type: String,
      default: null,
    },
  },
};
</script>
