<template lang='pug'>
  label.checkbox(:disabled='disabled')
    input.checkbox__button(
      type='checkbox'
      :class='{ disabled: disabled, checked: checked }'
      :disabled='disabled'
      :checked='checked'
    )
    | {{ text }}
</template>

<style lang="scss" scoped>
  $checkbox-size:   20px;

  $checked-width:   12px;
  $checked-height:  5px;
  $checked-top-fix: -2px;

  $checkbox-ingap:  3px;

  .checkbox {
    display: flex;

    width: fit-content;

    font-family: $light-font;
    line-height: $medium-context * 1.34;

    &__button {
      position: relative;

      display: flex;
      align-items: center;
      justify-content: center;
      box-sizing: border-box;
      min-width: $checkbox-size;
      max-width: $checkbox-size;
      min-height: $checkbox-size;
      max-height: $checkbox-size;

      margin: 0;
      margin-right: $big-gap;
      padding: $checkbox-ingap;

      border: $border-thin solid $border-color;
      border-radius: $tiny-border-rd;

      outline: 0;

      transition: $normal-tn-delay $normal-time-func;

      appearance: none;

      &:not(:checked):not([disabled]):not(.disabled):focus,
      &:not(:checked):not([disabled]):not(.disabled):hover {
        border: 0;

        box-shadow: $focused-box $focus-color;
      }

      &:checked {
        border: 0;

        background-color: $info-neutral-color;
      }

      &:checked::before {
        position: relative;
        top: $checked-top-fix;

        width: $checked-width;
        height: $checked-height;

        border-bottom: $border-thin + .5 solid $light-color;
        border-left: $border-thin + .5 solid $light-color;

        transform: rotate(-45deg);

        content: '';
      }

      &:checked:disabled {
        background-color: $disable-bg-second-color;
      }

      &:checked:disabled::before {
        border-color: $disable-text-second-color;
      }

      &:not(:checked):disabled {
        border: 0;

        background-color: $disable-bg-second-color;
      }
    }

    &.disabled {
      cursor: not-allowed;
    }
  }
</style>

<script>
export default {
  props: {
    text: {
      type: String,
      default: 'Checkbox',
      required: true,
    },
    checked: {
      type: Boolean,
    },
    disabled: {
      type: Boolean,
    },
  },
};
</script>
