<template lang='pug'>
  label.radio(:disabled='disabled')
    input.radio__button(
      type='radio'
      :class='{ disabled: disabled, checked: checked }'
      :disabled='disabled'
      :checked='checked'
    )
    | {{ text }}
</template>

<style lang="scss" scoped>
  $radio-size: 20px;

  .radio {
    display: flex;

    width: fit-content;

    font-family: $light-font;
    line-height: $medium-context * 1.34;

    &__button {
      box-sizing: border-box;

      min-width: $radio-size;
      max-width: $radio-size;
      min-height: $radio-size;
      max-height: $radio-size;

      margin: 0;
      margin-right: $big-gap;

      border: $border-thin solid $border-color;
      border-radius: 50%;

      outline: 0;

      transition: $normal-tn-delay $normal-time-func;

      appearance: none;

      &:not(:checked):not([disabled]):not(.disabled):focus,
      &:not(:checked):not([disabled]):not(.disabled):hover {
        border: 0;

        box-shadow: $focused-box $focus-color;
      }

      &:checked {
        border: $radio-size / 5 solid $light-color;

        background-color: $info-neutral-color;

        box-shadow: $focused-box-small $info-neutral-color;
      }

      &:checked:disabled {
        background-color: $disable-text-second-color;

        box-shadow: $focused-box-small $disable-text-second-color;
      }

      &:not(:checked):disabled {
        border: 0;

        background-color: $disable-bg-second-color;
      }
    }
  }
</style>

<script>
export default {
  props: {
    text: {
      type: String,
      required: true,
      default: 'Radio',
    },
    checked: {
      type: Boolean,
    },
    disabled: {
      type: Boolean,
    },
  },
};
</script>
