<template lang='pug'>
  .input-container(:class='{ disabled: disabled, hasError: hasError }')
    small.input__title(v-if='title') {{ title }}
    input.input(
      :placeholder='placeholder'
      :class='{ hasError: hasError }'
      :disabled='disabled'
    )
    sub.input__sub(v-if='sub') {{ sub }}
</template>

<style lang="scss" scoped>
  $input-width: 359px;
  $input-height: 58px;
  $icon-field:   24px;
  $error-icon: url('../../../../../public/img/icons/icon-exclamation.svg');

  .input-container {
    position: relative;

    max-width: $input-width;

    transition: $normal-tn-delay $normal-time-func;

    .input {
      position: relative;

      box-sizing: border-box;
      width: 100%;
      height: $input-height;
      padding: $medium-gap $small-gap;
      border: $border-thin solid $border-color;

      color: $opacity-dark-color;

      font-size: $medium-context;
      font-family: $default-font;

      outline: 0;

      transition: inherit;

      &__title {
        position: absolute;

        z-index: $top-lvl;

        padding-top: $border-thin * 3;
        padding-left: $border-thin + $small-gap;

        color: $opacity-color;

        font-size: $tiny-context;
        font-family: $default-font;
      }

      &__sub {
        padding-left: $border-thin + $small-gap;

        color: $dark-opacity-color;

        font-size: $low-context;
        font-family: $default-font;
      }

      &::placeholder {
        color: $opacity-dark-color;
      }

      &:not([disabled]):focus,
      .input-container:not(.disabled) {
        border-color: transparent;

        color: $dark-color;

        box-shadow: $focused-box $focus-color;
      }
    }

    &::before {
      position: absolute;
      top: $border-thin + $small-gap;
      right: $border-thin + $small-gap;

      z-index: $top-lvl;

      display: block;

      width: $icon-field;
      height: $icon-field;

      opacity: 0;

      transition: inherit;

      content: '';
    }

    &.disabled {

      .input,
      .input[disabled],
      .input__title {
        border-color: $disable-bg-color;

        color: $disable-text-color;

        background-color: $disable-bg-color;
      }
    }

    &.hasError {

      .input,
      .input.hasError {
        border-color: $error-color;
      }

      &::before {
        background-color: $light-color;
        background-image: $error-icon;

        animation: $normal-tn-delay FadeIn forwards;
      }
    }
  }
</style>

<script>
export default {
  props: {
    // eslint-disable-next-line
    title: {
      type: String,
      required: false,
    },
    placeholder: {
      type: String,
      required: true,
      default: 'Placeholder',
    },
    // eslint-disable-next-line
    sub: {
      type: String,
      required: false,
    },
    disabled: {
      type: Boolean,
    },
    hasError: {
      type: Boolean,
    },
  },
};
</script>
